<template>
  <div class="container">
    <h2 class="text-center">تحديث بيانات الطلاب</h2>

    <v-label>Site name</v-label>
    <v-text-field class="right" placeholder="Site name" v-model="siteData.siteName" />

    <br />
    <v-label>Name</v-label>
    <v-text-field class="right" placeholder="Name" v-model="siteData.name" />

    <br />
    <v-label>Phone number</v-label>
    <v-text-field class="right" placeholder="Phone" v-model="siteData.phone" />

    <br />
    <v-label>Address</v-label>
    <v-text-field class="right" placeholder="Address" v-model="siteData.address" />

    <br />
    <v-label>Career</v-label>
    <v-text-field class="right" placeholder="Career" v-model="siteData.career" />

    <br />
    <v-label>تعليمات الموقع</v-label>
    <p dir="rtl">ضع فاصلة (,) بين كل تعليم والاخر</p>
    <v-textarea
      class="right"
      v-model="siteData.instructions"
      placeholder="تعليمات الموقع"
    ></v-textarea>

    <br />
    <v-label>Facebook</v-label>
    <v-text-field class="right" placeholder="Facebook" v-model="siteData.facebook" />

    <br />
    <v-label>Email</v-label>
    <v-text-field class="right" placeholder="Email" v-model="siteData.email" />

    <br />
    <v-label>Whatsapp</v-label>
    <v-text-field class="right" placeholder="Telegram" v-model="siteData.whatsapp" />

    <br />
    <v-label>Telegram</v-label>
    <v-text-field class="right" placeholder="Whatsapp" v-model="siteData.telegram" />

    <br />
    <v-label>Youtube secret</v-label>
    <v-text-field
      class="right"
      placeholder="Youtube secret"
      v-model="siteData.youtubeSecret"
    />
    <a class="right" target="__blank" href="https://www.youtube.com/account_advanced"
      >احصل على المفتاح الخاص بقناتك على يوتيوب</a
    >

    <br />
    <v-label>طريقة الدفع</v-label>
    <v-text-field class="right" placeholder="طريقة الدفع" v-model="siteData.payMethod" />

    <br />
    <v-label>رقم خدمة الدفع</v-label>
    <v-text-field
      class="right"
      placeholder="رقمة خدمة الدفع"
