      <v-checkbox
        label="اظهار عدد طلاب المنصة"
        v-model="siteData.showStudentsCount"
      ></v-checkbox>
    </div>

    <br />
    <div dir="rtl">
      <v-checkbox
        label="السماح للطالب بالتسجيل مرة واحدة فقط لمنع استخدام الايميل من أكثر من طالب"
