    <br />
    <div dir="rtl">
      <v-checkbox label="تفعيل أكواد الدروس" v-model="siteData.lessonCodes"></v-checkbox>
    </div>
    <br />
    <div dir="rtl">
      <v-checkbox
        label="ترتيب الأسئلة عشوائيا"
        v-model="siteData.randomQuestions"
      ></v-checkbox>
    </div>
    <br />

    <div class="pick-images mt-4" dir="rtl">
      <v-label>اختر صورة للموقع</v-label>
      <input @change="pickImages" ref="files" type="file" class="form-control" multiple />
      <div class="display">
        <div class="row justify-content-center">
          <div class="col-md-5">
            <img
              width="100%"
              sizes="(min-width: 30em) 30em, 100vw"
              :src="images_displays"
              :alt="'img'"
              class="img-fluid"
            />
          </div>
        </div>
      </div>
    </div>

    <div class="pick-color mt-4" dir="rtl">
      <v-label>اختر لون مدرج من اللينك اسفله واطبع الكود في هذه الخانة</v-label>

      <a
        target="__blank"
        href="https://css-gradient.com"
        class="btn-block btn-outline text-right"
        >اختر اللون من هنا</a
      >
      <input
        @change="(e) => pickColor(e)"
        v-model="siteData.color"
        id="color"
        class="form-control"
        type="text"
        placeholder="ضع كود اللون هنا"
      />
    </div>

    <div class="language mt-4" dir="rtl">
      <v-label>المادة التي سوف تشرحها باللغة العربية أم الانجليزية؟</v-label>
      <select v-model="siteData.language" class="form-control">
        <option value="arabic">العربية</option>
        <option value="english">English</option>
      </select>
    </div>

    <br /><br />
    <div v-if="loading" class="d-flex justify-content-center align-items-center">
      <loading />
    </div>
    <div class="w-100 d-flex justify-content-center">
      <button class="button btn w-50" @click="submit">Update</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Loading from "../../components/loading";
export default {
  components: {
    Loading,
  },
  created() {
    axios.get("/getAdminData").then((res) => {
      if (res.data.adminData) {
        this.siteData = res.data.adminData;
        this.image = res.data.adminData.image1;
        this.images_displays = res.data.adminData.image1.replace("http", "https");
      }
    });
  },
  data() {
    return {
      siteData: {
        siteName: "",
        name: "",
        phone: "",
        address: "",
        career: "",
        facebook: "",
        email: "",
        whatsapp: "",
        telegram: "",
        youtubeSecret: "",
        payMethod: "",
        cashNumber: "",
        color: "",
        language: "arabic",
        instructions: "",
        showStudentsCount: false,
        activateStudentOneRegistration: false,
        deactiveStudentConfirmation: false,
        watermark: false,
        lessonCodes: false,
        randomQuestions: true,
      },
      image: "",
      images_displays: null,
      loading: false,
    };
  },
  methods: {
    submit() {
      const formData = new FormData();
      for (let data in this.siteData) {
        formData.append(data, this.siteData[data]);
      }
      formData.append("image", this.image);
      // formData.append("image2", this.images[1]);
      // formData.append("image3", this.images[2]);
      this.loading = true;
      axios.patch("/updateadminData", formData).then((res) => {
        console.log(res);
        this.loading = false;
        if (res.data.done) {
          this.$store.dispatch("writemessage", "تم تحديث البيانات بنجاح");
        } else {
          this.$store.dispatch("writemessage", "لم تقم بتحديث أى بيانات");
        }
      });
    },
    pickImages() {
      this.image = null;
      this.images_displays = null;
      const files = this.$refs.files.files;
      const file = files[0];
      // files.forEach((file) => {
      if (file.size > 5 * 1024 * 1024) {
        alert("حجم الصورة أكبر من المسموح به");
        return;
      }
      if (!["jpg", "jpeg", "png"].includes(file.type.split("/")[1])) {
        alert("مسموح فقط بالصور");
        return;
      }
      console.log(file);
      this.image = file;
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.images_displays = reader.result;
      };
      // });
    },
    pickColor(e) {
      const value = e.target.value;

      const color =
        "linear-gradient(" + value.split("background: linear-gradient(")[1].split(";")[0];
      const parts = color.split(",");
      const main = parts[0];
      const color1 = parts[1] + "b3";
      const color2 = parts[2].replace(")", "") + "d1)";
      this.siteData.color = [main, color1, color2].join(",");
    },
  },
};
</script>

